.item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 55px 30px;
  align-items: flex-start;
}

.item__code {
  font-size: 14px;
  line-height: 1;
  color: $text-light;
}

.item__title {
  margin: 10px 0 22px;

  font-size: 40px;
  line-height: 50px;
  font-weight: 400;
}

.item__form {
  padding: 60px 50px;
  background-color: $bg-second;

  color: #fff;
}

.item__price {
  display: block;
  margin-bottom: 15px;

  font-weight: 500;
  font-size: 40px;
  line-height: 1;
}

.item__sizes {
  color: #898989;
}

.item__row {
  display: grid;
  grid-template-columns: 150px 224px;
  grid-gap: 15px;
}

.item__desc {
  grid-column: 1/-1
}

.item__content {
  width: 595px;
  padding-top: 50px;

  h3 {
    margin: 25px 0 5px;

    font-size: 16px;
    line-height: 24px;
  }

  p {
    margin: 0 0 20px;

    font-size: 16px;
    line-height: 28px;
    color: $text-main;
  }

  a {
    border-bottom: 2px dashed $text-main;
    transition: $transition;

    font-size: 14px;
    line-height: 22px;
    color: $text-main;

    &:hover,
    &:focus {
      border-color: transparent;
    }
  }
}