.product {
  display: grid;
  grid-template-columns: 120px 280px 140px auto 20px;
  grid-template-rows: repeat(3, min-content);
  align-items: center;
  grid-gap: 10px 20px;
}

.product__pic {
  align-self: flex-start;
  grid-column: 1/2;
  grid-row: 1/4;
}

.product__title {
  margin: 0;
  grid-column: 2/3;
  grid-row: 1/2;

  font-size: 16px;
  line-height: 22px;
}

.product__info {
  margin: 0;
  grid-column: 2/3;
  grid-row: 2/3;

  font-size: 14px;
  line-height: 1;
  color: #737373;

  &--color {
    span {
      position: relative;
      display: inline-block;
      padding-left: 30px;
      vertical-align: top;

      i {
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
      }
    }
  }

  span {
    color: $text-main;
  }
}

.product__code {
  grid-column: 2/3;
  grid-row: 3/4;

  font-size: 12px;
  color: $text-light;
}

.product__del {
  grid-column: 5/6;
  grid-row: 1/2;
}

.product__counter {
  border: 1px solid #CFCFCF;
  width: 120px;
  height: 50px;

  button {
    height: 50px;
    width: 30px;
  }

  input {
    padding: 10px 5px;
    height: 50px;
    width: 60px;
  }
}